.AddresseeOpen, .PostOpen{
    color: #007bff;
    background-color: #007bff21;
    font-weight: 600;
}

.AddresseeClose, .PostClose {
    color: rgb(38, 38, 38);
    font-weight: 300;
}

/* Style for the noscript message */
noscript {
    text-align: center;
    padding: 20px;
    font-size: 18px;
    color: #FF0000;
}


/* Style for the main content area */
main {
    background-color: #FFF;
    display: flex;
}

/* Style for the header */
header {
    background-color: #007BFF;
    color: #FFF;
    font-size: 45px;
    padding: 20px 40px 20px 40px;
    margin: 0 0 20px 0;
    text-align: center;
}


/* Style for the inbox panel */
.inbox-panel {
    border: 1px solid #ccc;
    margin: auto;
    padding: 11px;
    width: 300px;
    max-height: 686px;
    overflow: auto;
    overflow-y: auto;
}

.inbox {
    margin-top: 10px;
}

.AddresseeClose, .AddresseeOpen {
    margin-bottom: 3px;
    padding: 3px 0 3px 10px;
    border-radius: 4px;
}

/* Style for the post details (open) */
.PostOpen {
    background-color: #007bff36;
}

.AddresseeClose, .AddresseeOpen, .Logout, .PostClose, .PostOpen {
    cursor: pointer;
}

.AddresseeClose:hover, .PostClose:hover {
    background-color: #007bff36;
    color: #007bff;
}

.Logout:hover {
    color: #007bff;
}

.PostClose, .PostOpen  {
    margin: 0 5px 5px 15px;
    padding: 2px 0 2px 10px;
    border-radius: 8px;
}

/* Style for the post content section */
.post_content {
    background-color: #fff;
    border: 1px solid #ccc;
    flex: 3 1;
    margin-left: 10px;
    overflow-wrap: anywhere;
    padding: 15px;
    scroll-padding: auto;
    overflow-y: revert;
    height: max-content;
    max-height: 686px;
    overflow: auto;
}

.post_content:hover, .inbox-panel:hover {
    border: 1px solid #007bff;
}

.actions {
    display: flex;
}

.inboxTitle {
    font-size: 50px;
    display: block;
    position: relative;
    width: fit-content;
    margin: 0;
    padding: 0;
}

.Logout {
    font-size: larger;
    margin: 0 0 0 79px;
    width: fit-content;
    padding: 0;
    display: block;
    height: fit-content;
}

.emptyDiv {
    position: absolute;
    width: 0;
    height: 0;
}

body {
    font-family: Arial, sans-serif;
    background-color: #f0f0f0;
    margin: 0;
    padding: 0;
    display: block;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

/* Style for the root container */
#root {
    height: 90%;
    max-width: 90%;
    margin: 0 auto;
    padding: 20px;
    background-color: #FFF;
    border: 1px solid #CCC;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Style for the authentication panel */
.authPanel {
    text-align: center;
    margin: auto;
    margin-bottom: 20px;
}

/* Style for the label */
label {
    display: block;
    font-weight: bold;
    margin-bottom: 20px;
    font-size: 40px;
}

/* Style for the input field */
.AuthKeyField {
    width: 180px;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #CCC;
    border-radius: 4px;
}

/* Style for the submit button */
.SubmitAuthKeyButton {
    background-color: #007BFF;
    color: #FFF;
    border: none;
    padding: 10px 45px;
    cursor: pointer;
    border-radius: 4px;
}

.post_content_hidden {
    display: none;
}

@media (max-width: 768px) {
    /* Здесь добавьте стили, специфичные для мобильных устройств */
    /* Например, измените размеры шрифтов, отступы и другие параметры, чтобы сделать контент более читаемым на мобильных экранах */
    
    /* Пример: уменьшаем размер шрифта для заголовка header */
    header {
        font-size: 30px;
    }

    /* Пример: увеличиваем отступы и уменьшаем ширину .inbox-panel */
    .inbox-panel {
        margin: auto;
        padding: 10px;
    }

    main {
        flex-direction: column;
    }
    

    /* Другие стили для мобильных устройств здесь */
}